@media (min-width: 811px) {
  .CategoryProductList {
    top: -56px;
    grid-column: 2;
    padding-bottom: 2.4rem;
    min-height: 100vh; } }

.CategoryProductList-Page {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: max-content;
  grid-column-gap: 1.2rem; }
  @media (min-width: 811px) and (max-width: 1024px) and (-webkit-min-device-pixel-ratio: 1) {
    .CategoryProductList-Page {
      grid-template-columns: repeat(3, 1fr); } }
  @media (max-width: 810px) {
    .CategoryProductList-Page {
      grid-column-gap: 1.4rem;
      grid-template-columns: 1fr 1fr;
      border: 0; } }
  @media (min-width: 811px) {
    .CategoryProductList-Page {
      grid-column: 2;
      margin-top: 1.2rem; } }
  @media (min-width: 811px) {
    .CategoryProductList-Page ~ .CategoryProductList-Page {
      margin-top: 0; } }

.CategoryProductList-ProductsMissing {
  padding: 1.2rem;
  text-align: center; }
  @media (max-width: 810px) {
    .CategoryProductList-ProductsMissing {
      padding: 1.4rem; } }

.CategoryProductList-More {
  min-height: 3.6rem; }
  @media (max-width: 810px) {
    .CategoryProductList-More {
      min-height: 4.2rem; } }

.CategoryProductList-LoadButton {
  background-color: var(--category-product-list-load-button-background);
  cursor: pointer;
  font-size: 13pt;
  margin-bottom: 1.2rem;
  padding: 1em 0;
  text-align: center; }
  @media (max-width: 810px) {
    .CategoryProductList-LoadButton {
      margin-bottom: 1.4rem; } }
