.ExpandableContentShowMore {
  text-align: left; }
  .ExpandableContentShowMore-ShowAllButton {
    text-transform: uppercase;
    margin-top: .6rem;
    font-size: 1.26rem; }
    .ExpandableContentShowMore-ShowAllButton::after {
      content: '';
      border-width: 0 0 1px 1px;
      border-style: solid;
      height: 6px;
      width: 6px;
      position: absolute;
      right: 0;
      top: 50%;
      transform: translate(calc(100% + 5px), -75%) rotate(-45deg); }
    .ExpandableContentShowMore-ShowAllButton:focus:not(:hover) {
      text-decoration: unset; }
    .ExpandableContentShowMore-ShowAllButton_state_isOpen::after {
      transform: translate(calc(100% + 5px), -25%) rotate(135deg); }
