.CategoryPagination {
  display: flex;
  justify-content: center;
  min-height: 56px;
  margin: auto;
  align-items: center;
  z-index: 2;
  width: min-content; }
  @media (min-width: 811px) {
    .CategoryPagination {
      order: 1; } }
  @media (max-width: 810px) {
    .CategoryPagination {
      padding-bottom: 0;
      display: none; } }
  .CategoryPagination-Icon {
    cursor: pointer;
    margin: 0;
    height: 35px;
    display: block;
    width: 35px; }
    .CategoryPagination-Icon::before, .CategoryPagination-Icon::after {
      content: '';
      position: absolute; }
    .CategoryPagination-Icon::before {
      width: 18px;
      height: 1px;
      top: 17px;
      right: 8px;
      background-color: var(--header-color); }
    .CategoryPagination-Icon::after {
      width: 8px;
      height: 8px;
      top: 13px;
      left: 10px;
      transform: rotate(-45deg);
      border-top: 1px solid var(--header-color);
      border-left: 1px solid var(--header-color); }
    .CategoryPagination-Icon:hover, .CategoryPagination-Icon:focus {
      --header-color: var(--color-pagination-link-active-text); }
    .CategoryPagination-Icon_isNext {
      transform: rotate(180deg); }
  .CategoryPagination-ListItem {
    margin-bottom: 0;
    padding-left: 0;
    min-width: 35px;
    display: block; }
    .CategoryPagination-ListItem::before {
      content: ''; }
    @media (max-width: 810px) {
      .CategoryPagination-ListItem:first-child {
        padding-left: 0; } }
    .CategoryPagination-ListItem:last-child {
      margin-bottom: 0; }
