:root {
  --color-pagination-link-text: #0a0a0a;
  --color-pagination-link-active-text: var(--primary-base-color); }

.CategoryPaginationLink {
  min-width: 35px;
  display: block;
  text-align: center;
  line-height: 35px;
  color: var(--color-pagination-link-text); }
  .CategoryPaginationLink:hover, .CategoryPaginationLink:focus {
    --color-pagination-link-text: var(--color-pagination-link-active-text);
    text-decoration: none; }
  .CategoryPaginationLink_isCurrent {
    --color-pagination-link-text: var(--color-pagination-link-active-text); }
