@media (min-width: 811px) {
  .CategorySort {
    margin-left: auto;
    min-width: 18rem;
    grid-column: 3; } }

.CategorySort-Select {
  margin-top: 0; }
  @media (max-width: 810px) {
    .CategorySort-Select label:first-of-type {
      font-weight: 600;
      font-size: 1.6rem;
      padding-bottom: 3px; } }
  @media (min-width: 811px) {
    .CategorySort-Select label:first-of-type {
      display: none; } }

.CategorySort-Placeholder {
  text-align: right;
  opacity: 0;
  animation: appear 100ms 300ms forwards; }
  @media (max-width: 810px) {
    .CategorySort-Placeholder {
      text-align: left; } }

@media (max-width: 810px) {
  .CategorySort .FieldSelect::after {
    left: 55px;
    bottom: 22px; } }

@media (max-width: 810px) {
  .CategorySort .FieldSelect-Select {
    position: absolute;
    top: -35px;
    opacity: 0; } }
