:root {
  --category-filter-overlay-desktop-color: #979797;
  --category-filter-overlay-selected-color: var(--primary-base-color);
  --category-filter-overlay-hover-color: var(--primary-light-color); }

@media (min-width: 811px) {
  .CategoryFilterOverlay {
    position: relative;
    grid-row: 1/6;
    width: 100%;
    opacity: 1;
    pointer-events: all;
    display: flex;
    flex-direction: column;
    height: auto;
    overflow: visible; } }

@media (max-width: 810px) {
  .CategoryFilterOverlay {
    transition: transform 200ms ease-in-out;
    transform: translateY(100%);
    will-change: transform; } }

.CategoryFilterOverlay_isVisible {
  transform: translateY(0%); }

.CategoryFilterOverlay-NoResults {
  margin: 0;
  padding: 1.2rem 1.2rem 0; }
  @media (max-width: 810px) {
    .CategoryFilterOverlay-NoResults {
      padding: 1.4rem 1.4rem 0; } }
  @media (min-width: 811px) {
    .CategoryFilterOverlay-NoResults {
      padding: 0 0 1.2rem 0; } }

.CategoryFilterOverlay-Heading {
  font-size: 1.8rem;
  font-weight: bold;
  text-transform: none;
  margin-top: 0; }
  @media (max-width: 810px) {
    .CategoryFilterOverlay-Heading {
      display: none; } }

@media (min-width: 811px) {
  .CategoryFilterOverlay-Item {
    margin-right: 1.2rem; } }

.CategoryFilterOverlay-Item_isSelected:not(.Swatch_type_color):not(.Swatch_type_cat) {
  --category-filter-overlay-desktop-color: var(--category-filter-overlay-selected-color); }

@media (min-width: 811px) {
  .CategoryFilterOverlay-Item:not(.Swatch_type_color):not(.Swatch_type_cat) {
    padding: 0 12px;
    border: 1px solid var(--category-filter-overlay-desktop-color);
    color: var(--category-filter-overlay-desktop-color);
    margin-right: 6px; } }

@media (min-width: 811px) {
  .CategoryFilterOverlay-Item:not(.Swatch_type_color):not(.Swatch_type_cat):hover {
    --category-filter-overlay-desktop-color: var(--category-filter-overlay-hover-color); } }

@media (min-width: 811px) {
  .CategoryFilterOverlay-Item_type_cat::before {
    content: '\2022';
    margin-right: 1.2rem;
    margin-left: 2px; } }

.CategoryFilterOverlay-Placeholder {
  padding: 1.2rem; }
  @media (max-width: 810px) {
    .CategoryFilterOverlay-Placeholder {
      padding: 1.4rem; } }

.CategoryFilterOverlay-Attributes {
  --option-margin: .6rem; }
  @media (max-width: 810px) {
    .CategoryFilterOverlay-Attributes {
      --option-margin: .7rem; } }
  @media (min-width: 811px) {
    .CategoryFilterOverlay-Attributes {
      --option-margin: 0 12px 12px 0; } }

.CategoryFilterOverlay-Filter {
  order: 50; }
  @media (min-width: 811px) {
    .CategoryFilterOverlay-Filter {
      margin-bottom: 1.2rem; } }

@media (max-width: 810px) {
  .CategoryFilterOverlay-SeeResults, .CategoryFilterOverlay-ResetButton {
    position: sticky;
    padding: 1.2rem;
    width: 100%;
    left: 0; } }

@media (max-width: 810px) {
  .CategoryFilterOverlay-SeeResults, .CategoryFilterOverlay-ResetButton {
    padding: 1.4rem; } }

@media (min-width: 811px) {
  .CategoryFilterOverlay-SeeResults {
    display: none; } }

@media (max-width: 810px) {
  .CategoryFilterOverlay-NoResults ~ .CategoryFilterOverlay-SeeResults {
    display: none; } }

.CategoryFilterOverlay-Button.Button {
  width: 100%; }

.CategoryFilterOverlay-ResetButton {
  margin-bottom: 1.2rem; }
  @media (max-width: 810px) {
    .CategoryFilterOverlay-ResetButton {
      margin-bottom: 1.4rem; } }
  @media (max-width: 810px) {
    .CategoryFilterOverlay-ResetButton {
      display: none; } }
  @media (max-width: 810px) {
    .CategoryFilterOverlay-NoResults ~ .CategoryFilterOverlay-ResetButton {
      display: block; } }

.CategoryFilterOverlay li {
  padding-left: 0; }
  .CategoryFilterOverlay li::before {
    content: none; }
  @media (max-width: 810px) {
    .CategoryFilterOverlay li:last-child {
      margin-bottom: .72rem; } }
  @media (max-width: 810px) {
    .CategoryFilterOverlay li:last-child {
      margin-bottom: .84rem; } }

@media (max-width: 810px) {
  .CategoryFilterOverlay .ProductAttributeValue-Image-Overlay {
    margin: .6rem; } }

@media (max-width: 810px) {
  .CategoryFilterOverlay .ProductAttributeValue-Image-Overlay {
    margin: .7rem; } }

.CategoryFilterOverlay .TextPlaceholder,
.CategoryFilterOverlay .ProductConfigurableAttributes-Placeholder {
  opacity: 0;
  animation: appear 100ms 300ms forwards; }
