:root {
  --search-item-divider-color: #eee;
  --search-item-title-color: #000; }

.SearchItem {
  padding: .6rem 0;
  margin-bottom: 0; }
  @media (max-width: 810px) {
    .SearchItem {
      padding: .7rem 0; } }
  @media (min-width: 811px) {
    .SearchItem {
      overflow: hidden; } }
  .SearchItem::before {
    content: none; }
  .SearchItem::after {
    content: '';
    position: absolute;
    bottom: -1px;
    left: 1.2rem;
    right: 1.2rem;
    height: 1px;
    background-color: var(--search-item-divider-color); }
    @media (max-width: 810px) {
      .SearchItem::after {
        left: 1.4rem;
        right: 1.4rem; } }
  .SearchItem:last-child {
    margin-bottom: 0; }
    .SearchItem:last-child::after {
      content: none; }
  .SearchItem-Wrapper {
    display: grid;
    grid-template-columns: 4.8rem calc(100% - 6rem);
    grid-column-gap: 1.2rem; }
    @media (max-width: 810px) {
      .SearchItem-Wrapper {
        grid-template-columns: 5.6rem calc(100% - 7rem);
        grid-column-gap: 1.4rem; } }
  .SearchItem-Content {
    display: flex;
    flex-direction: column;
    justify-content: center; }
  .SearchItem-CustomAttribute {
    text-transform: uppercase;
    font-weight: 200;
    font-size: .96rem;
    line-height: 1.44rem; }
    @media (max-width: 810px) {
      .SearchItem-CustomAttribute {
        font-size: 1.12rem;
        line-height: 1.68rem; } }
  .SearchItem-Title {
    color: var(--search-item-title-color);
    font-size: 1.32rem;
    font-weight: 600;
    max-width: calc(100vw - 8.4rem); }
    @media (max-width: 810px) {
      .SearchItem-Title {
        font-size: 1.54rem;
        max-width: calc(100vw - 9.8rem); } }
    .SearchItem-Title_isLoaded {
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap; }
  .SearchItem-Link {
    color: inherit; }
    .SearchItem-Link:hover {
      text-decoration: none; }
