:root {
  --new-products-per-page-count: 5; }

.NewProducts-Products {
  --gaps-count: calc(var(--new-products-per-page-count) - 1);
  display: grid;
  grid-column-gap: 1.2rem;
  grid-auto-flow: column dense;
  grid-auto-columns: calc((100% - var(--gaps-count) * 1.2rem) / var(--new-products-per-page-count));
  overflow: auto; }
  @media (max-width: 810px) {
    .NewProducts-Products {
      --new-products-per-page-count: 2;
      grid-column-gap: 1.4rem;
      grid-auto-columns: calc((100% - var(--gaps-count) * 1.4rem) / var(--new-products-per-page-count)); } }
  @media (min-width: 811px) and (max-width: 1024px) and (-webkit-min-device-pixel-ratio: 1) {
    .NewProducts-Products {
      --new-products-per-page-count: 4; } }
