:root {
  --product-list-load-button-background: var(--secondary-base-color); }

@media (min-width: 811px) {
  .ProductList {
    grid-column: 2;
    padding-bottom: 2.4rem;
    min-height: 100vh; } }

.ProductList-ProductsMissing {
  padding: 1.2rem;
  text-align: center; }
  @media (max-width: 810px) {
    .ProductList-ProductsMissing {
      padding: 1.4rem; } }

.ProductList-More {
  min-height: 3.6rem; }
  @media (max-width: 810px) {
    .ProductList-More {
      min-height: 4.2rem; } }

.ProductList-LoadButton {
  background-color: var(--category-product-list-load-button-background);
  cursor: pointer;
  font-size: 13pt;
  margin-bottom: 1.2rem;
  padding: 1em 0;
  text-align: center; }
  @media (max-width: 810px) {
    .ProductList-LoadButton {
      margin-bottom: 1.4rem; } }
