:root {
  --category-page-miscellaneous-min-height: 56px; }

@media (min-width: 811px) {
  .CategoryPage-Wrapper {
    display: grid;
    padding-top: 2.4rem; } }

@media (min-width: 811px) {
  .CategoryPage-Wrapper {
    grid-template-columns: 280px auto;
    grid-column-gap: 4.8rem; } }

@media (max-width: 810px) {
  .CategoryPage-Wrapper {
    padding: 0; } }

@media (max-width: 810px) {
  .CategoryPage-ProductListWrapper {
    padding-top: 56px; } }

.CategoryPage-ItemsCount {
  padding: 0; }
  @media (max-width: 810px) {
    .CategoryPage-ItemsCount {
      order: 1;
      font-size: 1.08rem;
      padding: .36rem 1.2rem 1.2rem 0;
      text-align: center;
      grid-column: span 2; } }
  @media (max-width: 810px) {
    .CategoryPage-ItemsCount {
      padding: .42rem 1.4rem 1.4rem 0;
      font-size: 1.26rem; } }

.CategoryPage-Filter {
  text-transform: uppercase;
  text-align: right;
  grid-column: 2;
  font-weight: 600;
  font-size: 1.6rem;
  min-height: 50px; }
  @media (min-width: 811px) {
    .CategoryPage-Filter {
      display: none; } }
  .CategoryPage-Filter::before, .CategoryPage-Filter::after {
    content: '';
    position: absolute;
    left: calc(90% - 68px);
    top: calc(50% - 5px); }
  .CategoryPage-Filter::before {
    right: 70%;
    width: 11px;
    height: 8px;
    background-color: var(--header-color);
    clip-path: polygon(5px 8px, 11px 0, 0 0, 5px 8px, 5px 6px, 2px 1.5px, 8.6px 1.5px, 5px 6px); }
  .CategoryPage-Filter::after {
    height: 5px;
    width: 1px;
    background-color: var(--header-color);
    transform: translate3d(5px, 6px, 0); }

.CategoryPage-Miscellaneous {
  z-index: 1;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  min-height: var(--category-page-miscellaneous-min-height);
  transform: translateY(0);
  transition: transform 200ms cubic-bezier(0.47, 0, 0.745, 0.715); }
  @media (max-width: 810px) {
    .CategoryPage-Miscellaneous {
      position: fixed;
      top: var(--header-total-height);
      left: 0;
      width: 100%;
      z-index: 90;
      background: #fff;
      border-bottom: 1px solid var(--primary-divider-color);
      grid-template-columns: repeat(2, 1fr); } }
  @media (min-width: 811px) and (max-width: 1024px) and (-webkit-min-device-pixel-ratio: 1) {
    .CategoryPage-Miscellaneous {
      grid-template-columns: repeat(2, 1fr); } }
  @media (min-width: 811px) and (max-width: 1023px) and (-webkit-min-device-pixel-ratio: 1) and (orientation: portrait) {
    .CategoryPage-Miscellaneous {
      grid-template-rows: 1fr 1fr; } }
  @media (max-width: 810px) {
    .hideOnScroll .CategoryPage-Miscellaneous {
      transform: translateY(-100%); } }
  @media (max-width: 810px) {
    .CategoryPage-Miscellaneous > * {
      padding-left: 1.4rem;
      padding-right: 1.4rem; } }

@media (max-width: 810px) {
  .CategoryPage-CMS {
    margin-top: calc(var(--header-total-height) + var(--category-page-miscellaneous-min-height));
    padding-left: 1rem;
    padding-right: 1rem; } }

@media (min-width: 811px) {
  .CategoryPage-CMS {
    margin-bottom: 10px; } }
