:root {
  --category-details-background: var(--secondary-base-color); }

.CategoryDetails {
  display: grid;
  grid-template-columns: auto minmax(100px, 18rem);
  grid-gap: 1.2rem;
  background-color: var(--category-details-background);
  margin-bottom: 1.2rem; }
  @media (max-width: 810px) {
    .CategoryDetails {
      grid-template-columns: auto minmax(100px, 21rem);
      grid-gap: 1.4rem;
      margin-bottom: 1.4rem;
      display: none; } }
  .CategoryDetails-Picture {
    mix-blend-mode: multiply;
    height: 100%;
    background: none; }
  .CategoryDetails-Heading {
    font-size: 2rem;
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: .6rem; }
    @media (max-width: 810px) {
      .CategoryDetails-Heading {
        margin-bottom: .7rem; } }
  .CategoryDetails-Description {
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 1.4rem;
    font-weight: 300;
    padding: 2.4rem; }
    @media (max-width: 810px) {
      .CategoryDetails-Description {
        padding: 2.8rem; } }
